/* Dropdown Styling */
.select-trigger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    background-color: white;
    cursor: pointer;
    font-size: 16px;
    width: 200px;
    position: relative;
    transition: border-color 0.3s ease;
}

.select-trigger:hover {
    border-color: #2563eb;
}

/* Dropdown Content */
.select-content {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: white;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 4px;
    z-index: 10;
}

.select-item {
    padding: 12px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.select-item:hover {
    background-color: #f1f5f9;
}

/* Tabs Styling */
.tabs-list {
    display: flex;
    gap: 16px;
    margin-bottom: 1rem;
    justify-content: center;
}

.tab-trigger {
    padding: 12px 24px;
    font-size: 16px;
    font-weight: 600;
    color: #4a5568;
    background-color: #edf2f7;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.tab-trigger[data-state='active'] {
    background-color: #2563eb;
    color: white;
}

/* Tooltip Styling */
.recharts-tooltip-wrapper {
    pointer-events: auto;
    background: white;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    padding: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Chart Alignment Adjustments */
.bar-chart-wrapper {
    flex: 1;
    min-width: 0;
}

.y-axis-label {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 8px;
    white-space: nowrap;
}

/* Adjust the Y-axis label and chart margin */
.bar-chart-container {
    display: flex;
    align-items: flex-start;
    gap: 8px;
}

.labels-container {
    width: 90px; /* Tightened width for better space usage */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.chart-title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 12px;
    text-align: left;
}
